@font-face {
    font-family: 'fontello';
    src: url('../fonts/fontello.eot?24292727');
    src: url('../fonts/fontello.eot?24292727#iefix') format('embedded-opentype'),
         url('../fonts/fontello.woff?24292727') format('woff'),
         url('../fonts/fontello.ttf?24292727') format('truetype'),
         url('../fonts/fontello.svg?24292727#fontello') format('svg');
    font-weight: normal;
    font-style: normal;
  }
   
   
  .icon {
    font-family: "fontello";
    font-style: normal;
    font-weight: normal;
    speak: none;
    display: inline-block;
    text-decoration: inherit;
    width: 1em;
    margin-right: .2em;
    text-align: center;
    /* opacity: .8; */
   
    /* For safety - reset parent styles, that can break glyph codes*/
    font-variant: normal;
    text-transform: none;
   
    /* fix buttons height, for twitter bootstrap */
    line-height: 1em;
   
    /* Animation center compensation - margins should be symmetric */
    /* remove if not needed */
    margin-left: .2em;
   
    /* You can be more comfortable with increased icons size */
    /* font-size: 120%; */
   
    /* Font smoothing. That was taken from TWBS */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
   
    /* Uncomment for 3D effect */
    /* text-shadow: 1px 1px 1px rgba(127, 127, 127, 0.3); */
  }

  @media screen and (max-width: 768px) {
    .icon {
      font-size: 1.5rem;
    }
  }